import React, { useState } from "react";

import { useHistory } from "react-router-dom";

import { useDispatch, useSelector } from "react-redux";
import { editPost } from "./../../redux/postsSlice";

export default function EditPost(urlParams) {
  const id = urlParams.match.params.postId;

  const history = useHistory();

  const dispatch = useDispatch();
  const posts = useSelector((state) => state.posts);
  // eslint-disable-next-line eqeqeq
  const currentPost = posts.find((obj) => obj.id == id);

  const [title, setTitle] = useState(currentPost.title);
  const [content, setContent] = useState(currentPost.content);

  function handleSubmit(e) {
    e.preventDefault();

    dispatch(
      editPost({
        id,
        title,
        content,
      })
    );
    history.push("/posts/" + id);
  }

  return (
    <div className="container mt-5">
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label className="col-form-label" htmlFor="post-title">
            Post Title:
          </label>
          <input
            type="text"
            className="form-control"
            required
            placeholder="Post Title"
            id="post-title"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
          />
        </div>

        <div className="form-group">
          <label htmlFor="exampleTextarea">Content:</label>
          <textarea
            className="form-control"
            id="exampleTextarea"
            rows="4"
            required
            value={content}
            onChange={(e) => setContent(e.target.value)}
          ></textarea>
        </div>
        <button type="submit" className="btn btn-info rounded-pill">
          Save Post
        </button>
      </form>
    </div>
  );
}
