import React from "react";

import { reactButtonClicked } from "./../../redux/postsSlice";
import { useDispatch } from "react-redux";

export default function ReactionButtons({ post }) {
  // for filling
  // document.querySelector(".icon-heart").name = "heart-sharp";

  const dispatch = useDispatch();

  const { reactions, currentUserReactions, id } = post;

  const { heart, like, smile } = reactions;
  const { hearted, liked, smiled } = currentUserReactions;

  function decideIcon(
    ifChecked,
    postId,
    classSelector,
    iconName,
    reactionType,
    currentReaction
  ) {
    if (ifChecked)
      return (
        <ion-icon
          class={classSelector}
          onClick={() =>
            dispatch(
              reactButtonClicked({ postId, reactionType, currentReaction })
            )
          }
          name={`${iconName}-sharp`}
        ></ion-icon>
      );
    return (
      <ion-icon
        class={classSelector}
        onClick={() =>
          dispatch(
            reactButtonClicked({ postId, reactionType, currentReaction })
          )
        }
        name={`${iconName}-outline`}
      ></ion-icon>
    );
  }

  const likeIcon = decideIcon(
    liked,
    id,
    "icon-like",
    "thumbs-up",
    "liked",
    liked
  );
  const heartIcon = decideIcon(
    hearted,
    id,
    "icon-heart",
    "heart",
    "hearted",
    hearted
  );
  const smileIcon = decideIcon(
    smiled,
    id,
    "icon-smile",
    "happy",
    "smiled",
    smiled
  );

  return (
    <div className="d-flex">
      <div className="d-flex flex-column align-items-center">
        {likeIcon}
        <p className="lead">{like}</p>
      </div>
      <div className="d-flex flex-column align-items-center">
        {heartIcon}
        <p className="lead">{heart}</p>
      </div>

      <div className="d-flex flex-column align-items-center">
        {smileIcon}
        <p className="lead">{smile}</p>
      </div>
    </div>
  );
}
