import React from "react";

import "./resources/bootswatch.css";
import "./App.css";

import Header from "./components/header/header.component";
import AddNewPost from "./components/addNewPost/addNewPost.component";
import ShowPosts from "./components/showPosts/ShowPosts.component";
import SinglePost from "./components/singlePost/SinglePost.component";
import EditPost from "./components/editPost/EditPost.component";

import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";

function App() {
  return (
    <BrowserRouter>
      <div className="App">
        <Header />

        <Switch>
          <Route exact path="/">
            <AddNewPost />
            <ShowPosts />
          </Route>

          <Route exact path="/posts/:postId" component={SinglePost} />
          <Route exact path="/editPost/:postId" component={EditPost} />

          <Redirect to="/" />
        </Switch>
      </div>
    </BrowserRouter>
  );
}

export default App;
