import React from "react";

import { Link } from "react-router-dom";

import { useSelector } from "react-redux";

export default function SinglePost(urlParams) {
  const posts = useSelector((state) => state.posts);
  const authors = useSelector((state) => state.authors);

  // eslint-disable-next-line eqeqeq
  const post = posts.find((ob) => ob.id == urlParams.match.params.postId);

  if (!post)
    return (
      <div className="container my-5">
        <div className="alert alert-info p-5 my-5 text-center">
          <div className="display-4">Requested Page Not Found!</div>
        </div>
      </div>
    );

  const { id, title, authorId, content } = post;
  const author = authors.filter((author) => author.id === authorId);

  return (
    <div className="container my-5">
      <div className="jumbotron">
        <div className="display-4">{title}</div>
        <p className="lead">{content}</p>
        <span>
          {" "}
          <i>By {author[0].name}</i>
        </span>
        <hr />
        <Link
          to={`/editPost/${id}`}
          className="btn btn-outline-warning rounded-pill mr-2"
        >
          Edit Post
        </Link>
        <Link to="/" className="btn btn-outline-info rounded-pill">
          Go Back
        </Link>
      </div>
    </div>
  );
}
