import React from "react";

import { TimeAgo } from "./../timeAgo_Copied/TimeAgo.component";
import ReactionButtons from "./../reactionButtons/ReactionButtons.component";

import { useSelector } from "react-redux";

import { Link } from "react-router-dom";

export default function ShowPosts() {
  let posts = useSelector((state) => state.posts);
  const authors = useSelector((state) => state.authors);

  return (
    <div className="container mt-5">
      <div className="display-4 mb-5">Posts</div>
      {posts.map(function (post) {
        const { id, title, authorId, content, date } = post;
        const author = authors.filter((author) => author.id === authorId);

        return (
          <div key={id} className="post p-5 mb-3">
            <h5>{title}</h5>
            <cite title="Source Title">By {author[0].name}</cite>
            <div className="lead">{content}</div>
            <Link
              className="btn btn-outline-info rounded-pill mt-4"
              to={`/posts/${id}`}
            >
              {" "}
              View Post{" "}
            </Link>
            <hr />
            <ReactionButtons post={post} />
            <div className="mt-2"></div>
            <TimeAgo timestamp={date} />
          </div>
        );
      })}
    </div>
  );
}
