import React, { useState } from "react";

import { useDispatch } from "react-redux";
import { addPost } from "./../../redux/postsSlice";
import { useSelector } from "react-redux";

export default function AddNewPost() {
  const dispatch = useDispatch();

  const [title, setTitle] = useState("");
  const [authorId, setAuthorId] = useState("");
  const [content, setContent] = useState("");

  function handleSubmit(e) {
    e.preventDefault();

    dispatch(addPost(title, authorId, content));
    setTitle("");
    setAuthorId("");
    setContent("");

    document.querySelector("#post-Id").selectedIndex = 0;
  }

  const authors = useSelector((state) => state.authors);
  const options = authors.map(({ id, name }) => (
    <option value={id} key={id}>
      {name}
    </option>
  ));

  return (
    <div className="container mt-5">
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label className="col-form-label" htmlFor="post-title">
            Post Title:
          </label>
          <input
            type="text"
            className="form-control"
            required
            placeholder="Post Title"
            id="post-title"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
          />
        </div>
        <div className="form-group">
          <label htmlFor="post-Id">Author</label>
          <select
            className="form-control"
            id="post-Id"
            onChange={(e) => setAuthorId(e.target.value)}
          >
            <option value=""></option>
            {options}
          </select>
        </div>

        <div className="form-group">
          <label htmlFor="post-content">Content:</label>
          <textarea
            className="form-control"
            id="post-content"
            rows="4"
            required
            value={content}
            onChange={(e) => setContent(e.target.value)}
          ></textarea>
        </div>
        <button type="submit" className="btn btn-info rounded-pill">
          Add Post
        </button>
      </form>
    </div>
  );
}
