import { createSlice, nanoid } from "@reduxjs/toolkit";

import { sub } from "date-fns";

const state = [
  {
    id: 2,
    title: "Second post",
    authorId: "1342uuiwns2",
    content: "Lorem ipsum dolor sit amet, consectetur adipisicing elit.",
    date: sub(new Date(), { minutes: 10 }).toISOString(),
    reactions: {
      like: 10,
      heart: 5,
      smile: 8,
    },
    currentUserReactions: {
      liked: false,
      hearted: true,
      smiled: false,
    },
  },
  {
    id: 1,
    title: "First post",
    authorId: "133aff2s31",
    content: "Lorem ipsum dolor sit amet, consectetur adipisicing elit.",
    date: sub(new Date(), { minutes: 100 }).toISOString(),
    reactions: {
      like: 100,
      heart: 69,
      smile: 120,
    },
    currentUserReactions: {
      liked: true,
      hearted: false,
      smiled: true,
    },
  },
];

const postsSlice = createSlice({
  name: "posts",
  initialState: state,
  reducers: {
    addPost: {
      reducer(state, action) {
        state.splice(0, 0, action.payload);
      },
      prepare(title, authorId, content) {
        return {
          payload: {
            id: nanoid(),
            title,
            authorId,
            content,
            date: new Date().toISOString(),
          },
        };
      },
    },
    reactButtonClicked: (state, action) => {
      const { postId, reactionType, currentReaction } = action.payload;
      const currentPostObj = state.find((obj) => obj.id === postId);
      currentPostObj.currentUserReactions[reactionType] = !currentReaction;

      let str = reactionType.slice(0, reactionType.length - 1); // liked -> like
      if (reactionType === "hearted") str = "heart";

      if (!currentReaction) currentPostObj.reactions[str] += 1;
      else currentPostObj.reactions[str] -= 1;
    },
    editPost: (state, action) => {
      // eslint-disable-next-line eqeqeq
      const currentPostObj = state.find((obj) => obj.id == action.payload.id);
      currentPostObj.title = action.payload.title;
      currentPostObj.content = action.payload.content;
    },
  },
});

export default postsSlice.reducer;

export const { addPost, editPost, reactButtonClicked } = postsSlice.actions;
